.topMenuBar 
{
  background-color: white;
  height: 40px;
  margin: 0px;
  padding: 0px;
  position: relative;
}

.mainCategories 
{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: top;
  color: #8f8f8f;
  height: 40px;
}

.topCat 
{
  margin: 0px;
  padding: 0px 10px;
  cursor: pointer;
  height: 40px;
  font-size: 1em;
  line-height: 40px;
  border-bottom: 4px solid transparent;
  transition: all 400ms;
  text-align: center;
  position: relative;
  background-color: white;
  z-index: 22;
}

.topCat:hover
{
  border-bottom: 4px solid orange;
  font-weight: normal;
  color: #4f4f4f;
}

.subCat 
{
  background-color: rgba(255, 255, 255, 0.9);
  color: #7f7f7f;
  position: relative;
  margin-top: 2px;
  padding: 20px 0px;
  transition: all 500ms;
  z-index: 21;
  overflow: hidden;
}

.subCat li
{
  padding: 10px 40px;
  cursor: pointer;
}

.subCat li span
{
  border-bottom: 1px solid transparent;
  transition: all 500ms;
}

.subCat li:hover span
{
  border-bottom: 1px solid orange;
}

.subActive
{
  top: 0;
  opacity: 1;
}

.subInactive
{
  top: -400px;
  opacity: 0;
}

.subCategories ul li
{
  text-align: center;
  padding: 10px;
}

.subVisible
{
  display: flex;
}

.subHidden 
{
  display: none;
}

@media (max-width: 858px)
{
  .topMenu {
    display: none;
  }
}

.filterProducts 
{
  display: flex;
  flex-direction: row;
  justify-content: right;
  padding: 20px 50px 10px 0px;
}

.filterSelector
{
  position: relative;
  display: flex;
  flex-direction: column;
}

.filterTitle, .filterName
{
  color: #7f7f7f;
  font-size: 1.1em;
  padding: 5px 10px;
}

.filterName 
{
  width: 240px;
  background-color: #eeeeee;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-radius: 4px;
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.angleDown 
{
  color: orange;
}

.filterList 
{
  width: 240px;
  position: absolute;
  top: 45px;
  z-index: 50;
  padding: 10px 20px;
  background-color: #eeeeee;
  border-radius: 4px;
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.4);
  overflow: hidden;
  transition: all 500ms;
}

.filterList li
{
  font-size: 1.1em;
  margin: 20px auto;
  color: #7f7f7f;
  cursor: pointer;
}

.filterList li:hover
{
  color: dodgerblue;
}

.showList 
{
  opacity: 1;
  height: auto;
  padding: 10px 20px;
}

.hideList
{
  opacity: 0;
  height: 0;
  padding: 0px 20px;
}

.applyFilter 
{
  display: inline-block;
  text-align: center;
  padding: 10px;
  background-color: orange;
  color: white;
  width: 100%;
  border-radius: 4px;
}

.fActive 
{
  display: inline;
  color: orange;
}

.fInactive
{
  display: none;
}

.dropDown 
{
  display: none;
}

@media (max-width: 858px){
  .dropDown 
  {
    position: absolute;
    top: 80px;
    left: 0px;
    width: 100%;
    background-color: #f8f8f8;
    z-index: 101;
    display: flex;
    flex-direction: column;
    transition: all 500ms;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.5);
  }

  .ddCategories 
  {
    font-size: 24px;
    font-family: 'ubuntu', 'cantarell', sans-serif;
    text-align: center;
  }

  .ddCategories li span
  {
    display: block;
    padding: 20px;
    border-top: 1px solid orange;
    color: #787878;
    cursor: pointer;
  }

  .ddCategories li:last-child span
  {
    border-bottom: 1px solid orange;
  }

  .ddSubcategories 
  {
    background-color: #eeeeee;
    overflow: hidden;
    transition: all 500ms;
  }

  .ddSubcategories li 
  {
    display: block;
    padding: 20px;
    font-size: 24px;
    font-family: 'ubuntu', 'cantarel', sans-serif;
    text-align: center;
    border-top: 1px solid #d0d0d0;
    color: #707070;
  }

  .ddOpenclose 
  {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-color: orange;
  }

  .ddMenuclose
  {
    display: block;
    width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
  }

  .ddHidden 
  {
    top: -1000px;
  }

  .ddVisible
  {
    top: 80px;
  }
  
  .smHidden 
  {
    max-height: 0px;
    transition: all 500ms;
  }

  .smVisible 
  {
    max-height: 100%;
    transition: all 500ms;
  }
}

