.reactModalOverlay {
  background-color: #ddddddb4;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: opacity 1000ms ease-in-out;
  opacity: 0.8;
  display: flex;
  justify-content: center;
  align-content: center;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

.reactModalContent {
  background-color: #f6f6f6;
  border-radius: 70px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  width: calc(100% - 10px);
  max-width: 650px;
  border-radius: 20px;
  margin: 30px auto;
  overflow: auto;
}

.reactModalContent::-webkit-scrollbar {
  display: none;
}

